import React from 'react';
import injectSheet from 'react-jss';
import Comet1 from './imgs/1.png';
import Comet2 from './imgs/2.png';
import Comet3 from './imgs/3.png';
import circleBack from './imgs/4.png';
import style from './style';
import './Home.css';

const Home = ({ classes }) => {
  return (
    <div className={classes.container}>
      <h2>Practice makes perfect</h2>
      <img src={Comet1} className={classes.spin1} alt="comet" />
      <img src={Comet2} className={classes.spin2} alt="comet" />
      <img src={Comet3} className={classes.spin3} alt="comet" />
      <img src={circleBack} className={classes.circle} alt="" />
    </div>
  );
};

Home.defaultProps = {
};

export default injectSheet(style)(Home);



