const style = {
  container: {
    display: 'flex',
    flex: 1,
    backgroundColor: '#000',
    height: window.innerHeight-50,
    justifyContent: 'center',
    alignItems: 'center',
    '& h2': {
      color: '#fff',
      fontFamily: 'Montserrat',
      letterSpacing: 3,
      fontSize: '3em',
      fontWeight: 600,
      textTransform: 'Uppercase',
      position: 'relative',
      zIndex: 999,
    },
  },
  circle: {
    width: '100%',
    maxWidth: 800,
    position: 'absolute',
  },
  spin1: {
    animation: 'spin infinite 4s linear',
    width: '60%',
    maxWidth: 600,
    position: 'absolute',
  },
  spin2: {
    animation: 'spin infinite 3s linear',
    width: '40%',
    maxWidth: 400,
    position: 'absolute',
  },
  spin3: {
    animation: 'spin infinite 2.5s linear',
    width: '50%',
    maxWidth: 500,
    position: 'absolute',
  },
};

export default style;



