const styles = {
  container: {
    backgroundColor: props => props.backgroundColor,
    margin: '0',
    padding: '0 40px',
    display: 'flex',
    height: 50,
    flex: 1,
    justifyContent: 'space-between',
    textTransform: 'uppercase',
  },
  logoAndIcon: {
    display: 'flex',
    flex: 1,
    padding: '15px 0',
    justifyContent: 'space-between',
    '& span': {
      fontFamily: 'Lato',
      color: props => props.color,
      cursor: 'default',
    },
  },
  icon: {
    display: 'none',
  },
  list: {
    margin: 0,
    padding: [15, 0],
    zIndex: 999,
  },
  list1: {
    extend: 'list',
  },
  listItem: {
    display: 'inline',
    '& a': {
      padding: [8, 10],
      color: props => props.color,
      textDecoration: 'none',
      fontWeight: 300,
    },
    '& a:hover': {
      boxShadow: props => `inset 0 -2px 0 0 ${props.hoverColor}`,
      transition: 'box-shadow 0.3s ease',
    },
  },
  '@media (max-width: 767px)': {
    icon: {
      display: 'block',
    },
    container: {
      flexDirection: 'column',
    },
    list: {
      padding: [0, 0, 15],
      maxHeight: 200,
    },
    list1: {
      padding: 0,
      maxHeight: 0,
      '& $listItem': {
        display: 'none',
      },
    },
    listItem: {
      display: 'block',
      padding: [10, 0],
      '& a': {
        padding: [10, 0],
        width: '100%',
        letterSpacing: 1,
        transition: 'letter-spacing 0.3s ease',
      },
      '& a:after': {
        display: 'inline-block',
        opacity: 0,
        transition: 'transform 0.3s, opacity 0.2s',
        marginLeft: 5,
        content: '" - "',
        transform: 'translate(-15px)',
      },
      '& a:hover': {
        boxShadow: 'none',
        letterSpacing: 2,
        transition: 'letter-spacing 0.3s ease',
      },
      '& a:hover:after': {
        opacity: 1,
        '-webkit-transform': 'translate(0px)',
        '-moz-transform': 'translate(0px)',
        transform: 'translate(0px)',
      },
    },
  },
};

export default styles;



