import React from 'react';
import injectSheet from 'react-jss';
import styles from './styles';

const MenuToggleIcon = ({ classes, isActive }) => (
  <div className={isActive ? classes.containerIsActive : classes.container} >
    <div className={classes.box} >
      <div className={classes.inner} />
    </div>
  </div>
);

MenuToggleIcon.defaultProps = {
  color: '#000',
  size: '24px',
};

export default injectSheet(styles)(MenuToggleIcon);



