import React, { Component } from 'react';
import injectSheet from 'react-jss';
import { Route, Link } from 'react-router-dom';
import MenuToggleIcon from './MenuToggleIcon';
import styles from './styles';

const MenuLink = ({ children, to, activeOnlyWhenExact }) => (
  <Route
    path={to}
    exact={activeOnlyWhenExact}
    children={({ match }) => (
      <Link to={to} className={match ? 'route--active' : ''} >
        {children}
      </Link>
    )}
  />
);

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      expandMenu: false,
    };
  }
  
  onExpandMenu = () => {
    this.setState({
      expandMenu: !this.state.expandMenu,
    });
  }

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.container} >
        <div className={classes.logoAndIcon}>
          <span>E A L O N</span>
          <div className={classes.icon} onClick={this.onExpandMenu} >
            <MenuToggleIcon
              size="16px"
              color="#14E1DB"
              isActive={this.state.expandMenu}
            />
          </div>
        </div>
        <ul className={this.state.expandMenu?classes.list:classes.list1}>
          <li className={classes.listItem}>
            <MenuLink activeOnlyWhenExact to="/">HOME</MenuLink>
          </li>
          <li className={classes.listItem}>
            <MenuLink to="/products">COMPONENTS</MenuLink>
          </li>
          <li className={classes.listItem}>
            <MenuLink to="/orders">NOTES</MenuLink>
          </li>
          <li className={classes.listItem}>
            <MenuLink to="/customers">ABOUT</MenuLink>
          </li>
        </ul>
      </div>
    );
  }
}

Header.defaultProps = {
  color: '#35B0E5',
  hoverColor: '#14E1DB',
  backgroundColor: '#000',
};

export default injectSheet(styles)(Header);



